import React from 'react'

import { CanMutate } from '../../../atoms/CanMutate'
import {
  DeliveryTypeEnum,
  useMOrderTimeNotification,
} from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { DeliveryTimeDialog } from '../../../molecules/DeliveryTimeDialog'
import { TouchButton } from '../../../molecules/TouchButton'

interface IProps {
  orderId: ID
  isDelivery: boolean
}

export const OrderStateTimeNotification: React.FC<IProps> = ({
  orderId,
  isDelivery,
}) => {
  const [sendTimeNotification] = useMOrderTimeNotification()

  return (
    <CanMutate mutationName="orderNotifyCustomer">
      <DeliveryTimeDialog
        title={
          isDelivery
            ? i18n.t`Send expected time of delivery to customer (in minutes)`
            : i18n.t`Send expected time of pickup to customer (in minutes)`
        }
        deliveryType={
          isDelivery ? DeliveryTypeEnum.Messenger : DeliveryTypeEnum.Pickup
        }
        onConfirm={minutes => {
          sendTimeNotification({ orderId, minutes })
        }}
      >
        {({ openDialog }) => (
          <TouchButton iconName="schedule" onClick={openDialog} />
        )}
      </DeliveryTimeDialog>
    </CanMutate>
  )
}
