import React from 'react'
import styled from 'styled-components'

import { DeliveryTypeEnum } from '../graph/generated'
import { TouchButton } from '../molecules/TouchButton'
import { Colors } from '../styling'

interface IProps {
  initialMinutes?: number
  deliveryType: DeliveryTypeEnum
  onMinuteClick(minute: number): void
}

const deliveryMinutes = [20, 30, 45, 60, 75, 90, 105, 120]
const pickupMinutes = [10, 20, 30, 45, 60, 75, 90]

const MinutesContainer = styled.div`
  display: flex;
  justify-content: center;
`
const MinutesItem = styled.div`
  margin: 1rem 1rem 0 0;
`

export const DeliveryMinutes: React.FC<IProps> = ({
  deliveryType,
  onMinuteClick,
  initialMinutes,
}) => {
  const [selected, setSelected] = React.useState(initialMinutes)
  const options = getMinutesForDeliveryType(deliveryType)
  return (
    <MinutesContainer>
      {options.map(minutes => (
        <MinutesItem key={minutes}>
          <TouchButton
            onClick={() => {
              setSelected(minutes)
              onMinuteClick(minutes)
            }}
            color={selected === minutes ? Colors.yellow : Colors.green}
          >
            {minutes}
          </TouchButton>
        </MinutesItem>
      ))}
    </MinutesContainer>
  )
}

export function getMinutesForDeliveryType(type: DeliveryTypeEnum) {
  return type === DeliveryTypeEnum.Pickup ? pickupMinutes : deliveryMinutes
}
