import { Trans } from '@lingui/macro'
import React from 'react'
import {
  AutoSizer,
  CellMeasurer,
  CellMeasurerCache,
  IndexRange,
  InfiniteLoader,
  List,
} from 'react-virtualized'

import { Loading } from '../../../atoms/Loading'
import { useRoot } from '../../../atoms/RootProvider'
import {
  OrderStateCategoryEnum,
  QStatsOrdersListDocument,
  QStatsOrdersListList,
  QStatsOrdersListQuery,
  QStatsOrdersListVariables,
  useQStatsOrdersCounts,
} from '../../../graph/generated'
import { useExecutableQuery } from '../../../hooks/useExecutableQuery'
import { OrderBox } from '../../order/atoms/OrderBox'
import { useStatsFilterCurrent } from '../hooks/useStatsFilter'

export const OrdersStatisticsList: React.FC = () => {
  const { statistics } = useRoot()
  const filter = useStatsFilterCurrent()

  const orders = React.useRef(new Map<number, QStatsOrdersListList>())
  const loaderRef = React.useRef<InfiniteLoader | null>(null)
  const measureCache = React.useMemo(
    () =>
      new CellMeasurerCache({
        fixedWidth: true,
        defaultHeight: 55,
        minHeight: 55,
      }),
    [],
  )

  const { data: countsData, loading: countsLoading } = useQStatsOrdersCounts(
    { filter: filter! },
    { skip: !filter },
  )

  React.useEffect(() => {
    orders.current.clear()
    measureCache.clearAll()
    if (loaderRef.current) {
      loaderRef.current.resetLoadMoreRowsCache(true)
    }
  }, [JSON.stringify(filter)])

  const { execute: executeList } = useExecutableQuery<
    QStatsOrdersListQuery,
    QStatsOrdersListVariables
  >(QStatsOrdersListDocument)

  const queryMoreOrders = React.useCallback(
    async ({ startIndex, stopIndex }: IndexRange) => {
      if (filter) {
        const result = await executeList({
          filter,
          pagination: {
            fromIndex: startIndex,
            toIndex: stopIndex,
          },
        })

        if (result.data) {
          const list = [...result.data.stats.orders.list]
          for (let index = startIndex; index <= stopIndex; index++) {
            const order = list.shift()
            if (order) {
              orders.current.set(index, order)
            }
          }
        }
      }
    },
    [filter],
  )

  const isRowLoaded = React.useCallback(
    ({ index }) => orders.current.get(index) !== undefined,
    [],
  )

  const rowRenderer = React.useCallback(({ index, key, style, parent }) => {
    const order = orders.current.get(index)
    if (order === undefined) {
      return (
        <div style={style} key={key}>
          ...
        </div>
      )
    }
    return (
      <CellMeasurer
        key={key}
        columnIndex={0}
        rowIndex={index}
        cache={measureCache}
        parent={parent}
      >
        {({ measure }) => (
          <div style={style}>
            <OrderBox
              order={order}
              showBranch={statistics.branchesFilter.length > 1}
              isCancelled={
                order.orderStateCategory === OrderStateCategoryEnum.Cancel
              }
              onToggleDetail={() => {
                setTimeout(measure, 500)
              }}
            />
          </div>
        )}
      </CellMeasurer>
    )
  }, [])

  if (!filter) {
    return null
  }

  if (countsLoading) {
    return <Loading />
  }

  const { finished, cancelled } = countsData.stats.counts
  const totalCount = finished + cancelled

  if (totalCount === 0) {
    return <Trans>No orders</Trans>
  }

  return (
    <AutoSizer>
      {({ width, height }) => (
        <InfiniteLoader
          rowCount={totalCount}
          isRowLoaded={isRowLoaded}
          loadMoreRows={queryMoreOrders}
          ref={loaderRef}
        >
          {({ onRowsRendered, registerChild }) => (
            <List
              ref={registerChild}
              onRowsRendered={onRowsRendered}
              rowHeight={measureCache.rowHeight}
              deferredMeasurementCache={measureCache}
              rowCount={totalCount}
              width={width}
              height={height}
              rowRenderer={rowRenderer}
            />
          )}
        </InfiniteLoader>
      )}
    </AutoSizer>
  )
}
