import React from 'react'

import { useStatsFilter } from '../hooks/useStatsFilter'

interface IProps {
  autoFilter?: string
  children: ReactNode | ReactNode[]
}

export const OrdersStatisticsLoader: React.FC<IProps> = ({
  children,
  autoFilter,
}) => {
  const { executeFilter, renderForm } = useStatsFilter()

  React.useEffect(
    () => {
      if (autoFilter) {
        executeFilter()
      }
    },
    [autoFilter],
  )

  return renderForm(children)
}
