import { Trans } from '@lingui/macro'
import React from 'react'

import { CanMutate } from '../../../atoms/CanMutate'
import { useMOrderChangeToPickup } from '../../../graph/generated'
import { ConfirmButton } from '../../../molecules/ConfirmButton'

interface IProps {
  orderId: ID
  isDelivery: boolean
}

export const OrderDeliveryToPickup: React.FC<IProps> = ({
  orderId,
  isDelivery,
}) => {
  const [changeToPickup] = useMOrderChangeToPickup()
  if (!isDelivery) {
    return null
  }
  return (
    <CanMutate mutationName="updateOrderDeliveryToPickup">
      <ConfirmButton
        confirmNode={<Trans>Really change?</Trans>}
        onConfirm={() => {
          changeToPickup({ orderId })
        }}
      >
        <Trans>Change to pickup</Trans>
      </ConfirmButton>
    </CanMutate>
  )
}
