import React from 'react'
import styled from 'styled-components'

import { IconLine } from '../../../atoms/IconLine'
import {
  OrderOriginEnum,
  QOrderDetailOrderOrigin,
} from '../../../graph/generated'
import { Colors } from '../../../styling'

interface IProps {
  displayedId: string
  orderOrigin: QOrderDetailOrderOrigin
}

const orderColorAccordingOrigin = ({ orderOrigin }: IProps) => {
  switch (orderOrigin.enum) {
    case OrderOriginEnum.DameJidlo:
      return Colors.orange
    default:
      return Colors.greyLight
  }
}

const OrderOriginLabel = styled.div`
  display: inline-block;
  background: ${orderColorAccordingOrigin};
  border-radius: 0.7rem;
  margin: 0 0 0 1rem;
  padding: 0.1rem 0.8rem;
`

export const OrderIdLine: React.FC<IProps> = ({ displayedId, orderOrigin }) => (
  <IconLine icon="format_list_numbered" labelMargin="0">
    {displayedId}
    <OrderOriginLabel orderOrigin={orderOrigin} displayedId={displayedId}>
      {orderOrigin.code}
    </OrderOriginLabel>
  </IconLine>
)
