import React from 'react'
import styled from 'styled-components'

import { HorizontalSeparator } from '../../../atoms/HorizontalSeparator'
import { Loading } from '../../../atoms/Loading'
import { useQOrderDetail } from '../../../graph/generated'
import { ContactLines } from '../molecules/ContactLines'
import { NoteLine } from '../molecules/NoteLine'
import { OrderCountLine } from '../molecules/OrderCountLine'
import { OrderDetailTotals } from '../molecules/OrderDetailTotals'
import { OrderIdLine } from '../molecules/OrderIdLine'
import { OrderRuleLines } from '../molecules/OrderRuleLines'
import { OrderStats } from '../molecules/OrderStats'
import { OrderTimeLine } from '../molecules/OrderTimeLine'
import { OrderWareList } from '../molecules/OrderWareList'
import { OrderStateFlow } from './OrderStateFlow'

interface IProps {
  orderId: string
}

const StyledOrderDetail = styled.div`
  padding: 0 0.5rem 0.5rem 1.1rem;
`

export const OrderDetail = React.memo<IProps>(({ orderId }) => {
  const { data, loading } = useQOrderDetail({
    orderId,
  })

  if (loading) {
    return <Loading />
  }

  const { order } = data

  return (
    <>
      <StyledOrderDetail>
        <HorizontalSeparator />
        <OrderWareList orderWares={order.items} />
        <OrderDetailTotals totals={order} />
        <OrderRuleLines rules={order.allAppliedRules}>
          <OrderCountLine
            current={order.customerOrdersCountCurrent}
            total={order.customerOrdersCountTotal}
          />
        </OrderRuleLines>
        <ContactLines
          phones={order.customer.phones}
          emails={order.customer.emails}
        />
        <NoteLine note={order.note} previousNote={order.previousNote} />
        <HorizontalSeparator />
        <OrderIdLine displayedId={order.id} orderOrigin={order.orderOrigin} />
        <HorizontalSeparator />
        <OrderTimeLine orderTiming={order.orderTiming} />
        <OrderStats order={order} />
      </StyledOrderDetail>
      <OrderStateFlow orderId={orderId} branchId={order.companyBranch.id} />
    </>
  )
})
