import { observer } from 'mobx-react-lite'
import React from 'react'

import { MonospaceOutput } from '../../../atoms/MonospaceOutput'
import { useRoot } from '../../../atoms/RootProvider'
import { useQClosureReport } from '../../../graph/generated'
import { i18n } from '../../../i18n'

interface IProps {
  reportId?: string
}

export const ClosureStatisticsContent = observer<IProps>(
  ({ reportId = '' }) => {
    const { statistics } = useRoot()
    const skip = !statistics.closureBranch
    const { data, loading } = useQClosureReport(
      {
        branchId: statistics.closureBranch!,
        reportId,
        showReport: Boolean(reportId),
      },
      { skip },
    )

    if (skip) {
      return <MonospaceOutput text={i18n.t`Select a branch`} />
    }

    if (loading) {
      return <MonospaceOutput text={i18n.t`Computing`} />
    }

    return (
      <MonospaceOutput
        text={reportId ? data.report!.plainData : data.lastReport.plainData}
      />
    )
  },
)
