import { subDays } from 'date-fns'
import React, { createContext, useContext } from 'react'
import { Form } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import {
  QClosureHistoryDocument,
  QClosureHistoryHistory,
  QClosureHistoryQuery,
  QClosureHistoryVariables,
} from '../../../graph/generated'
import { formatDateISO } from '../../../helpers/formats'
import {
  IQueryResult,
  useExecutableQuery,
} from '../../../hooks/useExecutableQuery'

export type TReports = QClosureHistoryHistory

const context = createContext<IQueryResult<QClosureHistoryQuery> | null>(null)

export const useClosureHistory = () => useContext(context)!

export const ClosureHistoryLoader: React.FC = ({ children }) => {
  const { statistics } = useRoot()
  const { result, execute } = useExecutableQuery<
    QClosureHistoryQuery,
    QClosureHistoryVariables
  >(QClosureHistoryDocument)

  return (
    <Form
      onSubmit={(formValues: { dateFrom: Date; dateTo: Date }) => {
        if (statistics.closureBranch) {
          execute({
            branchId: statistics.closureBranch,
            dateFrom: formatDateISO(formValues.dateFrom),
            dateTo: formatDateISO(formValues.dateTo),
          })
        }
      }}
      defaultValues={{ dateFrom: subDays(new Date(), 1), dateTo: new Date() }}
    >
      <context.Provider value={result}>{children}</context.Provider>
    </Form>
  )
}
