import {
  DateTimePicker,
  DateTimePickerProps,
  InlineDateTimePicker,
} from 'material-ui-pickers'
import React from 'react'
import { Field, FieldType } from 'react-form'

import { i18n } from '../i18n'
import { DateTimeFormatProvider } from './DateTimeFormatProvider'

interface IProps extends Omit<DateTimePickerProps, 'value' | 'onChange'> {
  field: FieldType
  inline?: boolean
}

export const FormInputDateTime: React.FC<IProps> = ({
  field,
  inline = true,
  ...pickerProps
}) => {
  const Picker = inline ? InlineDateTimePicker : DateTimePicker
  return (
    <DateTimeFormatProvider>
      <Field
        field={field}
        render={fieldApi =>
          React.createElement(Picker, {
            value: fieldApi.value,
            onChange: value => fieldApi.setValue(value),
            onOpen: () => fieldApi.setTouched(true),
            format: 'lll',
            ampm: false,
            showTabs: false,
            maxDateMessage: i18n.t`Date is too much in the future`,
            minDateMessage: i18n.t`Date is too much in the past`,
            ...pickerProps,
          })
        }
      />
    </DateTimeFormatProvider>
  )
}
