import React from 'react'
import { RouteComponentProps } from 'react-router-dom'

import { Card } from '../../../atoms/Card'
import { i18n } from '../../../i18n'
import { FullPageLayout } from '../../../molecules/FullPageLayout'
import { ClosureStatisticsContent } from '../molecules/ClosureStatisticsContent'
import { ClosureStatisticsFilter } from '../molecules/ClosureStatisticsFilter'
import { ClosureStatisticsControl } from '../organisms/ClosureStatisticsControl'

interface IRouteParams {
  reportId?: string
}

interface IProps extends RouteComponentProps<IRouteParams> {}

export const ClosurePage: React.FC<IProps> = ({ match }) => (
  <FullPageLayout
    pageTitle={i18n.t`Closure`}
    useBackButton={true}
    backRoute="/overview"
  >
    <Card>
      <ClosureStatisticsFilter showCurrent={false} />
      <ClosureStatisticsControl showCurrent={Boolean(match.params.reportId)} />
    </Card>
    <ClosureStatisticsContent reportId={match.params.reportId} />
  </FullPageLayout>
)
