import React from 'react'
import styled from 'styled-components'

import { SizerButton } from '../atoms/SizerButton'
import { Colors, Radiuses, Spacing } from '../styling'
import { TouchButton } from './TouchButton'

interface IProps {}

const StyledHamburgerMenu = styled.div`
  position: relative;
`

const PopupMenu = styled.div`
  position: absolute;
  bottom: 4rem;
  left: 0;
  background: ${Colors.white};
  padding: ${Spacing.Small};
  border: solid 1px ${Colors.greyLight};
  border-radius: ${Radiuses.Card};
  box-shadow: 2px 6px 21px -2px rgba(0, 0, 0, 0.75);
  & div:not(:last-child) {
    margin-bottom: ${Spacing.Middle};
  }
`

export const HamburgerMenu: React.FC<IProps> = ({ children }) => {
  const [isOpened, setIsOpened] = React.useState(false)
  return React.Children.count(children) ? (
    <StyledHamburgerMenu>
      <SizerButton>
        <TouchButton
          iconName="menu"
          disabled={children === null}
          onClick={() => setIsOpened(!isOpened)}
        />
      </SizerButton>
      {isOpened && <PopupMenu>{children}</PopupMenu>}
    </StyledHamburgerMenu>
  ) : null
}
