import { isoDate } from '@speedlo/graphql'
import React from 'react'
import { Form } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import { InputStatisticCreate } from '../../../graph/types'

const context = React.createContext<InputStatisticCreate | undefined>(undefined)

export const useStatsFilterCurrent = () => React.useContext(context)

export function useStatsFilter() {
  const { statistics } = useRoot()
  const [filter, setFilter] = React.useState<InputStatisticCreate | undefined>()

  const executeFilter = () => {
    setFilter({
      branches: [...statistics.branchesFilter],
      drivers: [...statistics.drivers],
      dateTimeFrom: isoDate(statistics.dateTimeFrom),
      dateTimeTo: isoDate(statistics.dateTimeTo),
    })
  }

  const defaultValues = {
    dateTimeFrom: statistics.dateTimeFrom,
    dateTimeTo: statistics.dateTimeTo,
  }

  const renderForm = (children: ReactNode | ReactNode[]) => {
    return (
      <Form
        onSubmit={executeFilter}
        onChange={({ values }) => statistics.updateFilter(values)}
        defaultValues={defaultValues}
      >
        <context.Provider value={filter}>{children}</context.Provider>
      </Form>
    )
  }

  return {
    filter,
    executeFilter,
    renderForm,
  }
}
