import React from 'react'

import { HorizontalSeparator } from '../../../atoms/HorizontalSeparator'
import { IconLine } from '../../../atoms/IconLine'
import { LinkStyled } from '../../../atoms/LinkStyled'
import { FEmailFragment, FPhoneFragment } from '../../../graph/generated'

interface IContactLinesProps {
  phones: ReadonlyArray<FPhoneFragment>
  emails: ReadonlyArray<FEmailFragment>
}

type Contact = {
  icon: string
  isMail: boolean
  value: string
} & (FPhoneFragment | FEmailFragment)

export const ContactLines: React.FC<IContactLinesProps> = ({
  phones,
  emails,
}) => {
  const renderClickableLink = (contact: Contact) =>
    contact.isMail ? (
      <LinkStyled href={`mailto:${contact.value}`}>{contact.value}</LinkStyled>
    ) : (
      <LinkStyled href={`tel:${contact.value.replace('/ /g', '')}`}>
        {contact.value}
      </LinkStyled>
    )

  const renderContact = (contact: Contact) => (
    <IconLine icon={contact.icon} key={contact.value} labelMargin="0">
      <div title={contact.description || ''}>
        {renderClickableLink(contact)}
      </div>
    </IconLine>
  )
  return (
    <>
      {(phones.length > 0 || emails.length > 0) && <HorizontalSeparator />}
      {phones.map(phone =>
        renderContact({
          icon: 'phone',
          isMail: false,
          value: phone.phone,
          ...phone,
        }),
      )}
      {emails.map(email =>
        renderContact({
          icon: 'mail_outline',
          isMail: true,
          value: email.email,
          ...email,
        }),
      )}
    </>
  )
}
