import { Trans } from '@lingui/macro'
import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from '@material-ui/core'
import React, { MutableRefObject, useState } from 'react'

import { DeliveryMinutes } from '../atoms/DeliveryMinutes'
import { DeliveryTypeEnum } from '../graph/generated'
import { TouchButton } from './TouchButton'

interface IProps {
  title: string
  deliveryType: DeliveryTypeEnum
  cancelText?: string
  onConfirm(minutes: number): void
  onCancel?(): void
  refApi?: MutableRefObject<Maybe<IDeliveryTimeDialogApi>>
  children?(props: IDeliveryTimeDialogApi): React.ReactElement<{}>
}

export interface IDeliveryTimeDialogApi {
  openDialog(): void
  closeDialog(): void
}

export const DeliveryTimeDialog: React.FC<IProps> = ({
  title,
  deliveryType,
  onConfirm,
  onCancel,
  children,
  cancelText,
  refApi,
}) => {
  const [isOpen, setOpen] = useState(false)

  const openDialog = () => setOpen(true)
  const closeDialog = () => setOpen(false)

  if (refApi) {
    refApi.current = { openDialog, closeDialog }
  }

  return (
    <>
      {children && children({ openDialog, closeDialog })}
      <Dialog open={isOpen} onClose={closeDialog}>
        <DialogTitle>{title}</DialogTitle>
        <DialogContent>
          <DeliveryMinutes
            deliveryType={deliveryType}
            onMinuteClick={minutes => {
              closeDialog()
              onConfirm(minutes)
            }}
          />
        </DialogContent>
        <DialogActions>
          <TouchButton
            onClick={() => {
              closeDialog()
              if (onCancel) {
                onCancel()
              }
            }}
          >
            {cancelText ? cancelText : <Trans>Cancel</Trans>}
          </TouchButton>
        </DialogActions>
      </Dialog>
    </>
  )
}
