import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { useAutorunWhenSingleBranch } from '../../../hooks/useAutorunWhenSingleBranch'
import { CompanyBranchPicker } from '../../../organisms/CompanyBranchPicker'

interface IProps {}

export const StatisticsBranchFilter: React.FC<IProps> = () => {
  const { statistics } = useRoot()
  useAutorunWhenSingleBranch(branch => {
    statistics.setBranchFilter([branch.id])
  })
  return useObserver(() => (
    <CompanyBranchPicker
      selectedBranches={Array.from(statistics.branches)}
      onSelectionChange={statistics.setBranchFilter}
      onSelectionClear={statistics.clearBranchFilter}
      isMulti={true}
      hideWhenSingleBranch={true}
    />
  ))
}
