import React, { Fragment } from 'react'

import { QOrderDetailItems } from '../../../graph/generated'
import { OrderWareLine } from './OrderWareLine'

interface IOrderWareListProps {
  orderWares: ReadonlyArray<QOrderDetailItems>
}

export const OrderWareList: React.FC<IOrderWareListProps> = ({
  orderWares,
}) => {
  const renderWare = (orderWare: QOrderDetailItems) => (
    <Fragment key={orderWare.id}>
      <OrderWareLine
        code={orderWare.recipe.code}
        label={orderWare.recipe.nameLabel}
        price={orderWare.price.value}
      />
      <>
        {orderWare.sidedishes.map(sidedish => (
          <OrderWareLine
            key={sidedish.id}
            code={`+${sidedish.recipe.code}`}
            label={sidedish.recipe.nameLabel}
            price={sidedish.price.value}
          />
        ))}
      </>
    </Fragment>
  )
  return <div>{orderWares.map(renderWare)}</div>
}
