import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { MonospaceOutput } from '../../../atoms/MonospaceOutput'
import { Price } from '../../../atoms/Price'
import { useQStatsOrders } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { useStatsFilterCurrent } from '../hooks/useStatsFilter'

const StatPanel = styled.div`
  margin: 1.4rem 4rem 0 0.5rem;
  display: inline-block;
`

const StatLabel = styled.h1`
  font-size: 1.5rem;
  font-weight: normal;
  margin: 0;
`

const StatValue = styled.h2`
  font-size: 4rem;
  font-weight: normal;
  margin: 0;
`

export const OrdersStatisticsSummary = observer(() => {
  const filter = useStatsFilterCurrent()
  const { data, loading } = useQStatsOrders(
    { filter: filter! },
    { skip: !filter },
  )

  if (!filter) {
    return null
  }

  if (loading) {
    return <MonospaceOutput text={i18n.t`Computing`} />
  }

  const stats = data.stats.finishedOrders

  return (
    <>
      <StatPanel>
        <StatLabel>
          <Trans>Total turnover</Trans>
        </StatLabel>
        <StatValue>
          <Price
            price={stats.total.value}
            currencyCode={stats.total.currency.code}
          />
        </StatValue>
      </StatPanel>
      <StatPanel>
        <StatLabel>
          <Trans>Online payments</Trans>
        </StatLabel>
        <StatValue>
          <Price
            price={stats.onlinePayments.value}
            currencyCode={stats.onlinePayments.currency.code}
          />
        </StatValue>
      </StatPanel>
      <StatPanel>
        <StatLabel>
          <Trans>Orders count</Trans>
        </StatLabel>
        <StatValue>{stats.finishedCount + stats.cancelledCount}</StatValue>
      </StatPanel>
      <StatPanel>
        <StatLabel>
          <Trans>Total commissions</Trans>
        </StatLabel>
        <StatValue>
          <Price
            price={stats.fees.value}
            currencyCode={stats.fees.currency.code}
          />
        </StatValue>
      </StatPanel>
      <StatPanel>
        <MonospaceOutput text={data.stats.orderStatisticsPlain} />
      </StatPanel>
    </>
  )
})
