import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { FOrderTickerFragment } from '../../../graph/generated'
import { Colors, Radiuses } from '../../../styling'
import { useOrderListModel } from '../hooks/useOrderListModel'
import { OrderTicker } from '../molecules/OrderTicker'
import { OrderDetail } from '../organisms/OrderDetail'

interface IProps {
  order: FOrderTickerFragment
  showBranch: boolean
  isCancelled: boolean
  onToggleDetail?(): void
}

export const OrderBoxStyled = styled.div`
  background-color: ${Colors.white};
  color: ${Colors.greyDarkest};
  border-radius: ${Radiuses.Card};
  margin-bottom: 0.5rem;
  position: relative;
`

export const OrderBox = observer<IProps>(
  ({ order, showBranch, isCancelled, onToggleDetail }) => {
    const orderListModel = useOrderListModel()

    const orderId = order.id

    const onClick = React.useCallback(() => {
      orderListModel.toggleOrderDetail(orderId)
      if (onToggleDetail) {
        onToggleDetail()
      }
    }, [orderId])

    const isDetailOpen = orderListModel.isOrderDetailOpen(orderId)

    return (
      <OrderBoxStyled id={`order-${orderId}`}>
        <OrderTicker
          order={order}
          showBranch={showBranch}
          isCancelled={isCancelled}
          onClick={onClick}
        />
        {isDetailOpen && <OrderDetail orderId={orderId} />}
      </OrderBoxStyled>
    )
  },
)
