import React from 'react'
import { components } from 'react-select'
import { StylesConfig } from 'react-select/lib/styles'
import styled from 'styled-components'

import { Icon } from '../../../atoms/Icon'
import { SelectControl } from '../../../atoms/SelectControl'
import { FDriverFragment } from '../../../graph/generated'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'
import { Colors, Radiuses, Spacing } from '../../../styling'

type TDriver = FDriverFragment

interface IProps {
  drivers: ReadonlyArray<TDriver>
  selectedDriver: Maybe<TDriver>
  isLoading: boolean
  onSelect(driver: TDriver): void
  onClear(): void
  onReload(): void
}

const DriverName = styled.div`
  margin: 0.1rem 0 0rem 0.2rem;
  display: inline-block;
  vertical-align: top;
  font-size: 1.25rem;
`

const backgroundColor = {
  background: Colors.greyMiddle,
}
const textColor = { color: Colors.white }

const styles: StylesConfig = {
  control: base => ({
    ...base,
    ...backgroundColor,
    ...textColor,
    height: Spacing.ButtonHeight + Spacing.DefaultSizeUnit,
    borderWidth: 0,
    borderRadius: Radiuses.Button,
    margin: 0,
  }),
  menu: base => ({
    ...base,
    ...backgroundColor,
  }),
  valueContainer: base => ({
    ...base,
    ...textColor,
  }),
  singleValue: base => ({
    ...base,
    ...textColor,
  }),
  placeholder: base => ({
    ...base,
    ...textColor,
    fontSize: '1.2rem',
  }),
}

export const OrderDriverSelect: React.FC<IProps> = ({
  drivers,
  selectedDriver,
  isLoading,
  onSelect,
  onClear,
  onReload,
}) => (
  <SelectControl<TDriver>
    options={drivers as TDriver[]}
    value={selectedDriver}
    getOptionValue={RM.prop('id')}
    isLoading={isLoading}
    isSearchable={false}
    isClearable={true}
    placeholder="Deliver order"
    width={16}
    styles={styles}
    noOptionsMessage={() => i18n.t`No drivers available`}
    onChange={driver => {
      if (driver === null) {
        onClear()
      } else {
        onSelect(driver as TDriver)
      }
    }}
    onMenuOpen={onReload}
    components={{
      ValueContainer: props => (
        <>
          <Icon iconName="directions_car" fontSize={2} color={Colors.white} />
          <components.ValueContainer {...props} />
        </>
      ),
      DropdownIndicator: () => null,
    }}
    formatOptionLabel={driver => (
      <DriverName>{`${driver.firstName} ${driver.lastName}`}</DriverName>
    )}
  />
)
