import { i18nMark } from '@lingui/core'
import React from 'react'

import { CanMutate } from '../../../atoms/CanMutate'
import { OrderStateEnum, useMOrderState } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'

interface IProps {
  orderId: ID
  nextState: OrderStateEnum
}

const stateLabels = {
  [OrderStateEnum.Accepted]: i18nMark('Accept order'),
  [OrderStateEnum.ReadyToDeliver]: i18nMark('Ready to deliver'),
  [OrderStateEnum.Finished]: i18nMark('Finish order'),
}

export const OrderStateButton: React.FC<IProps> = ({ orderId, nextState }) => {
  const [updateOrderState] = useMOrderState()

  return (
    <CanMutate mutationName="addOrderState">
      <TouchButton
        text={i18n._(stateLabels[nextState])}
        width="7.375rem"
        onClick={() => {
          updateOrderState({ orderId, state: nextState })
        }}
      />
    </CanMutate>
  )
}
