import React from 'react'
import styled from 'styled-components'

interface IProps {
  text: string
}

const MonospaceOutputStyled = styled.p`
  color: white;
  font-family: 'Lucida Console', Monaco, monospace;
  white-space: pre-wrap;
  margin: 0.2rem 0.5rem 0.5rem 1rem;
  line-height: 1.8rem;
`

const formatOutput = (output: string) =>
  output.replace(new RegExp('\t', 'g'), '\u0009')

export const MonospaceOutput: React.FC<IProps> = ({ text }) => (
  <MonospaceOutputStyled>{formatOutput(text)}</MonospaceOutputStyled>
)
