import React from 'react'
import styled from 'styled-components'

import { FOrderTotalsFragment } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { OrderWarePriceLine } from './OrderWarePriceLine'

interface IProps {
  totals: FOrderTotalsFragment
}

const StyledOrderDetailTotals = styled.div`
  margin: 0 0 0 auto;
  width: 20rem;
  text-align: right;
`

export const OrderDetailTotals: React.FC<IProps> = ({ totals }) => (
  <StyledOrderDetailTotals>
    <OrderWarePriceLine label={i18n.t`Tips`} price={totals.tip.value} />
    <OrderWarePriceLine
      label={i18n.t`Transport fee`}
      price={totals.transportFee.value}
    />
    <OrderWarePriceLine
      label={i18n.t`Covers price`}
      price={totals.coverPrice.value}
    />
    <OrderWarePriceLine
      label={i18n.t`Discount total`}
      price={totals.totalDiscount.value * -1}
    />
    <OrderWarePriceLine
      label={i18n.t`Total price`}
      price={totals.totalSum.value}
      highlight={true}
    />
  </StyledOrderDetailTotals>
)
