import { Trans } from '@lingui/macro'
import React from 'react'

import { Headline } from '../../../atoms/Headline'
import { MonospaceOutput } from '../../../atoms/MonospaceOutput'
import { InputStatisticCreate, useQStatsDaily } from '../../../graph/generated'
import { i18n } from '../../../i18n'
import { useStatsFilterCurrent } from '../hooks/useStatsFilter'

const StatsDisplay: React.FC<{ plain: string }> = ({ plain, children }) => {
  return (
    <>
      <Headline light={true}>{children}</Headline>
      <MonospaceOutput text={plain} />
    </>
  )
}

export const DailyStatisticsContent: React.FC = () => {
  const filter = useStatsFilterCurrent()
  const getRelevantFilter = (): InputStatisticCreate => {
    if (!filter) {
      return { branches: [] }
    }
    return {
      branches: filter.branches,
      dateTimeFrom: filter.dateTimeFrom,
      dateTimeTo: filter.dateTimeTo,
    }
  }
  const { data, loading } = useQStatsDaily(
    {
      filter: getRelevantFilter(),
    },
    { skip: !filter },
  )

  if (!filter) {
    return null
  }

  if (loading) {
    return <MonospaceOutput text={i18n.t`Computing`} />
  }

  const { stats } = data
  return (
    <>
      <StatsDisplay plain={stats.daily}>
        <Trans>Daily statistics</Trans>
      </StatsDisplay>

      <StatsDisplay plain={stats.category}>
        <Trans>By category</Trans>
      </StatsDisplay>

      <StatsDisplay plain={stats.delivery}>
        <Trans>By type</Trans>
      </StatsDisplay>

      <StatsDisplay plain={stats.drivers}>
        <Trans>By driver</Trans>
      </StatsDisplay>
    </>
  )
}
