import { Plural, Trans } from '@lingui/react'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { FlexColumn } from '../../../atoms/FlexColumn'
import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { FormInputDateTime } from '../../../atoms/FormInputDateTime'
import { useRoot } from '../../../atoms/RootProvider'
import { FormSubmit } from '../../../molecules/FormSubmit'

export const StatisticsFilter = () => {
  const { statistics } = useRoot()
  return useObserver(() => (
    <FlexRowSingle justify="flex-start">
      <FlexColumn fixedWidth={true} width="12rem">
        <FormInputDateTime
          field="dateTimeFrom"
          label={<Trans>From</Trans>}
          autoOk={true}
        />
      </FlexColumn>
      <FlexColumn fixedWidth={true} width="12rem">
        <FormInputDateTime
          field="dateTimeTo"
          label={<Trans>To</Trans>}
          autoOk={true}
        />
      </FlexColumn>
      <FlexColumn>
        <FormSubmit width="15rem">
          <Plural
            value={statistics.branchesFilter.length}
            one="Show for one branch"
            few="Show for # branches"
            other="Show for # branches"
          />
        </FormSubmit>
      </FlexColumn>
    </FlexRowSingle>
  ))
}
