import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { useRoot } from '../../../atoms/RootProvider'
import { useAutorunWhenSingleBranch } from '../../../hooks/useAutorunWhenSingleBranch'
import { CompanyBranchPicker } from '../../../organisms/CompanyBranchPicker'
import { ClosureCurrentButton } from './ClosureCurrentButton'

const ButtonToRight = styled.div`
  float: right;
  margin: 0.5rem;
`

interface IProps {
  showCurrent: boolean
}

export const ClosureStatisticsFilter = observer<IProps>(({ showCurrent }) => {
  const { statistics } = useRoot()
  useAutorunWhenSingleBranch(branch => {
    statistics.setClosureBranch(branch.id)
  })
  return (
    <>
      <CompanyBranchPicker
        selectedBranches={
          statistics.closureBranch ? [statistics.closureBranch] : []
        }
        onSelectionChange={([branch]) => statistics.setClosureBranch(branch)}
        onSelectionClear={() => statistics.setClosureBranch(null)}
        isClearable={false}
        hideWhenSingleBranch={true}
      />
      {showCurrent && (
        <ButtonToRight>
          <ClosureCurrentButton />
        </ButtonToRight>
      )}
    </>
  )
})
