import React from 'react'

import { CanMutate } from '../../../atoms/CanMutate'
import {
  OrderStateCategoryEnum,
  OrderStateEnum,
  useMOrderState,
  useMRemoveOrderDriver,
  useMSelectOrderDriver,
  useQOrderDrivers,
} from '../../../graph/generated'
import { useCanQuery } from '../../../hooks/useAccessGuard'
import { OrderDriverSelect } from '../atoms/OrderDriverSelect'

interface IProps {
  branchId: ID
  orderId: ID
  orderStateCategory: OrderStateCategoryEnum
}

export const OrderStateDeliver: React.FC<IProps> = ({
  branchId,
  orderId,
  orderStateCategory,
}) => {
  const canRead = useCanQuery('drivers')

  const [deliverOrder] = useMOrderState()
  const [selectDriver] = useMSelectOrderDriver()
  const [removeDriver] = useMRemoveOrderDriver()

  const { data, loading, refetch } = useQOrderDrivers(
    {
      branchId: [branchId],
      orderId,
    },
    {
      skip: !canRead,
    },
  )
  return (
    <CanMutate mutationName="createUserDeliveringOrder">
      <OrderDriverSelect
        drivers={loading || !canRead ? [] : data.drivers}
        selectedDriver={
          loading ? null : data.order.driver && data.order.driver.user
        }
        isLoading={loading}
        onReload={refetch}
        onClear={() => {
          const orderDriver = data.order.driver
          if (orderDriver) {
            removeDriver({ orderDriverId: orderDriver.id })
          }
        }}
        onSelect={driver => {
          selectDriver({ orderId, driverId: driver.id })
          if (orderStateCategory !== OrderStateCategoryEnum.OnWay) {
            deliverOrder({ orderId, state: OrderStateEnum.OnWay })
          }
        }}
      />
    </CanMutate>
  )
}
