import { LocationDescriptor } from 'history'
import React from 'react'
import { Link, LinkProps, Route } from 'react-router-dom'
import resolvePathname from 'resolve-pathname'

interface IRelativeLinkProps extends LinkProps {
  to: LocationDescriptor
  from?: string
}

export const RelativeLink: React.FC<IRelativeLinkProps> = ({
  to,
  from,
  ...linkProps
}) => (
  <Route
    children={({ location }) => (
      <Link
        to={resolveLocation(to, from || `${location.pathname}/`)}
        {...linkProps}
      />
    )}
  />
)

export function resolveLocation(to: LocationDescriptor, from: string) {
  if (typeof to === 'string') {
    return resolvePathname(to, from)
  }
  const resolved = resolvePathname(to.pathname || '', from)
  return { ...to, pathname: resolved }
}
