import { Trans } from '@lingui/macro'
import { Timelapse } from '@material-ui/icons'
import React from 'react'
import styled from 'styled-components'

import { IconLine } from '../../../atoms/IconLine'
import { QOrderDetailOrder } from '../../../graph/generated'
import { formatTimeWithoutTodayDate } from '../../../helpers/formats'
import { Colors } from '../../../styling'
import { OrderTimeLabel } from '../atoms/OrderTimeLabel'

interface IProps {
  order: QOrderDetailOrder
}

const DelayedDeliveryTimeLabel = styled.span`
  font-weight: bold;
  color: ${Colors.orange};
`

const DelayedOrderTimeLabel = styled(OrderTimeLabel)`
  color: ${Colors.orange};
`

export const OrderStats: React.FC<IProps> = ({ order }) => {
  const stats: ReactNode[] = []
  if (order.delayedDeliveryTime) {
    stats.push(
      <div key="delayedDeliveryTime">
        <DelayedDeliveryTimeLabel>
          {formatTimeWithoutTodayDate(order.delayedDeliveryTime)}
        </DelayedDeliveryTimeLabel>
        <DelayedOrderTimeLabel>
          <Trans>Deliver at time</Trans>
        </DelayedOrderTimeLabel>
      </div>,
    )
  } else if (order.deliverAt) {
    stats.push(
      <div key="deliverAt">
        {formatTimeWithoutTodayDate(order.deliverAt)}
        <OrderTimeLabel>
          <Trans>Deliver on time</Trans>
        </OrderTimeLabel>
      </div>,
    )
  }

  if (order.driver) {
    const { firstName, lastName } = order.driver.user
    stats.push(
      <div key="driver">
        {`${firstName} ${lastName}`}
        <OrderTimeLabel>
          <Trans>Driver</Trans>
        </OrderTimeLabel>
      </div>,
    )
  }
  if (stats.length === 0) {
    return null
  }
  return (
    <IconLine icon={Timelapse} labelMargin="0">
      {stats}
    </IconLine>
  )
}
