import React from 'react'

import { HorizontalSeparator } from '../../../atoms/HorizontalSeparator'
import { IconLine } from '../../../atoms/IconLine'

interface IOrderRuleLinesProps {
  rules: ReadonlyArray<string>
}

export const OrderRuleLines: React.FC<IOrderRuleLinesProps> = ({
  rules,
  children,
}) =>
  rules.length > 0 || children ? (
    <>
      <HorizontalSeparator />
      <IconLine icon="announcement" labelMargin="0">
        {children}
        <br />
        {rules.join(', ')}
      </IconLine>
    </>
  ) : null
