import {
  ApolloOperationError,
  useApolloClient,
  useApolloDefender,
} from '@speedlo/graphql'
import { useReaction } from '@speedlo/hooks'
import { ApolloError, QueryOptions } from 'apollo-client'
import { DocumentNode } from 'graphql'
import { runInAction } from 'mobx'
import { useObservable } from 'mobx-react-lite'

export interface IQueryResult<TData> {
  data: Maybe<TData>
  loading: boolean
  error?: ApolloError
  hasAnyError: boolean
  wasExecuted: boolean
}

export function useExecutableQuery<TData, TVariables>(
  query: DocumentNode,
  options: Partial<QueryOptions<TVariables>> = {},
) {
  const client = useApolloClient()
  const { onError } = useApolloDefender()
  const result: IQueryResult<TData> = useObservable({
    data: null,
    loading: false,
    error: undefined,
    wasExecuted: false,
    get hasAnyError() {
      return Boolean(result.error)
    },
  })
  useReaction(
    () => result.error,
    error => {
      if (error) {
        onError(error)
      }
    },
  )
  const makeError = (err: ApolloError, variables: TVariables) => {
    return new ApolloOperationError(err, query, { ...options, variables })
  }
  const execute = async (variables: TVariables) => {
    result.loading = true
    try {
      const queryResult = await client.query<TData, TVariables>({
        ...options,
        query,
        variables,
      })
      runInAction(() => {
        result.wasExecuted = true
        result.data = queryResult.data
        result.loading = false
        if (queryResult.errors && queryResult.errors.length > 0) {
          result.error = makeError(
            new ApolloError({ graphQLErrors: queryResult.errors }),
            variables,
          )
        }
      })
    } catch (err) {
      runInAction(() => {
        result.error = makeError(err, variables)
        result.loading = false
      })
    }
    return result
  }
  const reset = () => {
    runInAction(() => {
      result.wasExecuted = false
      result.data = null
      result.error = undefined
      result.loading = false
    })
  }
  return { result, execute, reset }
}
