import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { SelectControl } from '../../../atoms/SelectControl'
import { FDriverFragment, useQAllDrivers } from '../../../graph/generated'
import { formatFullName } from '../../../helpers/formats'
import { RM } from '../../../helpers/ramda'
import { i18n } from '../../../i18n'

type TDriver = FDriverFragment

export const StatisticsDriversFilter = observer(() => {
  const { statistics } = useRoot()
  const { data, loading } = useQAllDrivers({
    branches: statistics.branchesFilter,
  })
  React.useEffect(
    () => {
      if (!loading) {
        statistics.setDriversFilter(
          RM.intersection(statistics.drivers, data.drivers.map(RM.prop('id'))),
        )
      }
    },
    [loading],
  )
  return (
    <SelectControl<FDriverFragment>
      isMulti={true}
      isClearable={true}
      isSearchable={true}
      filterOption={(option, rawInput) =>
        rawInput.length === 0 ||
        formatFullName(option.data)
          .toLocaleLowerCase()
          .includes(rawInput.toLocaleLowerCase())
      }
      placeholder={i18n.t`Filter by drivers`}
      isLoading={loading}
      options={loading ? [] : [...data.drivers]}
      value={
        loading
          ? []
          : data.drivers.filter(driver =>
              statistics.drivers.includes(driver.id),
            )
      }
      formatOptionLabel={formatFullName}
      getOptionValue={RM.prop('id')}
      onChange={value => {
        if (!value) {
          statistics.setDriversFilter([])
        } else {
          statistics.setDriversFilter((value as TDriver[]).map(RM.prop('id')))
        }
      }}
    />
  )
})
