import React from 'react'

import { PageLayout } from '../../../molecules/PageLayout'
import { PageLayoutItem } from '../../../molecules/PageLayoutItem'
import { ClosureButton } from '../molecules/ClosureButton'
import { DailyStatisticsButton } from '../molecules/DailyStatisticsButton'
import { OrdersStatisticsButton } from '../molecules/OrdersStatisticsButton'

interface IProps {}

function getLayout() {
  return [
    { i: 'OrdersStatistics', x: 0, y: 1, w: 2, h: 2 },
    { i: 'DailyStatistics', x: 2, y: 1, w: 2, h: 2 },
    { i: 'Closure', x: 4, y: 1, w: 2, h: 2 },
  ]
}

export const OverviewPage: React.FC<IProps> = props => (
  <PageLayout layouts={getLayout()}>
    <PageLayoutItem
      key="OrdersStatistics"
      route="/overview/orders"
      component={OrdersStatisticsButton}
      canQuery="statistics"
    />
    <PageLayoutItem
      key="DailyStatistics"
      route="/overview/statistics"
      component={DailyStatisticsButton}
      canQuery="statistics"
    />
    <PageLayoutItem
      key="Closure"
      route="/overview/closure"
      component={ClosureButton}
      canQuery="lastFinancialReport"
    />
  </PageLayout>
)
