import React from 'react'
import { Route, RouteComponentProps, Switch } from 'react-router-dom'

import { PageTitle } from '../../../atoms/PageTitle'
import { i18n } from '../../../i18n'
import { NotFoundPage } from '../../../pages/NotFoundPage'
import { SecureRoute } from '../../../routes/SecureRoute'
import { ClosureHistoryPage } from '../pages/ClosureHistoryPage'
import { ClosurePage } from '../pages/ClosurePage'
import { DailyStatisticsPage } from '../pages/DailyStatisticsPage'
import { OrdersStatisticsPage } from '../pages/OrdersStatisticsPage'
import { OverviewPage } from '../pages/OverviewPage'

interface ISettingsRoutesProps extends RouteComponentProps<{}> {}

export const OverviewRoutes: React.FC<ISettingsRoutesProps> = ({ match }) => (
  <>
    <PageTitle title={i18n.t`Overview`} />
    <Switch>
      <SecureRoute exact={true} path={match.path} component={OverviewPage} />
      <SecureRoute
        path={`${match.path}/closure/history`}
        component={ClosureHistoryPage}
      />
      <SecureRoute
        path={`${match.path}/closure/:reportId?`}
        component={ClosurePage}
      />
      <SecureRoute
        path={`${match.path}/statistics`}
        component={DailyStatisticsPage}
      />
      <SecureRoute
        path={`${match.path}/orders/:filter?`}
        component={OrdersStatisticsPage}
      />
      <Route component={NotFoundPage} />
    </Switch>
  </>
)

export default OverviewRoutes
