import React from 'react'
import { RouteComponentProps } from 'react-router-dom'

import { Card } from '../../../atoms/Card'
import { AutoHeight } from '../../../atoms/FlexAutoHeight'
import { useRoot } from '../../../atoms/RootProvider'
import { i18n } from '../../../i18n'
import { PageLayout } from '../../../molecules/PageLayout'
import { PageLayoutFullHeightItem } from '../../../molecules/PageLayoutFullHeightItem'
import { OrdersStatisticsList } from '../molecules/OrdersStatisticsList'
import { OrdersStatisticsSummary } from '../molecules/OrdersStatisticsSummary'
import { StatisticsBranchFilter } from '../molecules/StatisticsBranchFilter'
import { StatisticsDriversFilter } from '../molecules/StatisticsDriversFilter'
import { StatisticsFilter } from '../molecules/StatisticsFilter'
import { OrdersStatisticsLoader } from '../organisms/OrdersStatisticsLoader'

interface IProps extends RouteComponentProps<{ filter?: string }> {}

function getLayout() {
  return [{ i: 'summary', x: 0, y: 1, w: 7, h: 1 }]
}

export const OrdersStatisticsPage: React.FC<IProps> = ({ match }) => {
  const { statistics } = useRoot()
  if (match.params.filter === 'today') {
    statistics.setTodayOrdersFilter()
  }
  return (
    <OrdersStatisticsLoader autoFilter={match.params.filter}>
      <PageLayoutFullHeightItem
        withScrollArea={false}
        row={1}
        column={7}
        widthInColumns={9}
      >
        <AutoHeight>
          <OrdersStatisticsList />
        </AutoHeight>
      </PageLayoutFullHeightItem>

      <PageLayout
        layouts={getLayout()}
        pageTitle={i18n.t`Orders statistics`}
        showSoundControl={false}
        scrollPageTitle={false}
        useBackButton={true}
      >
        <div key="summary">
          <Card>
            <StatisticsBranchFilter />
            <StatisticsDriversFilter />
            <StatisticsFilter />
          </Card>

          <OrdersStatisticsSummary />
        </div>
      </PageLayout>
    </OrdersStatisticsLoader>
  )
}
